/**
 * Redirect to the preview of module's H5P activities
 *
 * @module      local_nolej
 * @author      Vincenzo Padula <vincenzo@oc-group.eu>
 * @copyright   2025 OC Open Consulting SB Srl
 * @license     http://www.gnu.org/copyleft/gpl.html GNU GPL v3 or later
 */
define(['jquery', 'core/ajax', 'core/notification'], function($, Ajax, Notification) {
    $('[data-action="preview"]').click(function() {
        let documentid = $(this).attr('data-documentid');
        var request = {
            methodname: 'local_nolej_preview',
            args: {
                documentid: documentid
            }
        };

        Ajax.call([request])[0].done(function(data) {
            if (data.link) {
                // Redirect to the preview.
                window.location.href = data.link;
            } else {
                // Preview not available, show error message.
                Notification.addNotification({
                    message: data.message,
                    type: 'error'
                });
            }
        }).fail(Notification.exception);
    });
});
